  width: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  margin: 5px auto;
  padding: 1rem 3rem 1rem 0rem;
  color: #446aab;
  font-size: 1.15rem;
  font-weight: 400;
  border-bottom: 1px solid #e5e5e5;
}

.accordion .conte a:hover,
.accordion .accordion-link:hover::after {
  cursor: pointer;
  color: #446aab;
}

.accordion .conte a:hover::after {
  border: 1px solid #446aab;
}

.accordion .conte a.active {
  color: #446aab;
  border-bottom: 1px solid #446aab;
}

.accordion .accordion-link::after {
  font-family: "Ionicons";
  content: url(../img/Arrow-down-g-new.svg);
  position: absolute;
  float: right;
  right: 1rem;
  font-size: 1rem;
  color: #2d6cfe;
  padding: 1px;
  width: 36px;
  height: 36px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 1px solid #2d6cfe;
  text-align: center;
  line-height: 38px;
  -webkit-transition: all 0.2s ease 0.15s;
  -o-transition: all 0.2s ease 0.15s;
  transition: all 0.2s ease 0.15s;
}

.accordion .accordion-link.active::after {
  font-family: "Ionicons";
  content: url(../img/Arrow-down-f-new.svg);
  transform: rotate(180deg);
  color: #2d6cfe;
  border: 1px solid #2d6cfe;
  line-height: 35px;
}

.accordion .conte {
  opacity: 0;
  padding: 0 1rem;
  max-height: 0;
  border-bottom: 1px solid #e5e5e5;
  overflow: hidden;
  clear: both;
}

