/* Slideshow container */
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px 23px;
  color: #084c8d;
  font-size: 18px;
  transition: 0.6s ease;
  background: #ffffff;
  border: 1px solid #f0f6ff;
  box-sizing: border-box;
  border-radius: 50%;
  margin-left: -80px;
}

/* Position the "next button" to the right */
.next {
  right: -90px;
  border-radius: 50%;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: #f0f6ff;
}

