/* чекбокс */
#menu__toggle {
  opacity: 0;
}

/* кнопка */
.menu__btn {
  display: none;
  align-items: center;
  position: absolute;
  top: 30px;
  width: 21px;
  margin-right: 4px;
  cursor: pointer;
  z-index: 2;
}

/* гамбургер */
.menu__btn > span {
  display: flex;
  position: absolute;
  width: 100%;
  height: 2px;
  transition: all 0.2s ease-in-out;
}

.menu__btn > span::before,
.menu__btn > span::after {
  display: flex;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #2d6cfe;
  transition: all 0.2s ease-in-out;
}

.menu__btn > span::before {
  content: "";
  top: -4px;
}

.menu__btn > span::after {
  content: "";
  top: 6px;
}

#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}

#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
  background-color: #2d6cfe;
}

#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
  background-color: #2d6cfe;
}

#menu__toggle:checked ~ .menu__box {
  visibility: visible;
  top: 58px;
  z-index: 1;
  padding-top: 20px;
  color: #ffffff;
  text-transform: uppercase;
}

.hamburger-menu {
  display: flex;
  justify-content: right;
  overflow: hidden;
}

.menu__box {
  display: block;
  position: absolute;
  visibility: hidden;
  top: -100%;
  right: 0;
  height: 100vh;
  width: 100%;
  margin: 0;
  padding: 80px 0 30px;
  list-style: none;
  text-align: center;
  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(50px);
  transition: all 0.1s ease-in-out;
}
.menu__box > li > a {
  font-weight: 700;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.02em;
  font-family: Manrope, sans-serif;
  text-transform: uppercase;
  color: #393a66;
}
.menu__box a {
  padding: 5px 5px;
}
.menu__box ul {
  padding: 0;
}
.menu__box li {
  position: relative;
  display: flex;
  justify-content: center;
  width: 87%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
}
.menu__box li:hover > .sub-menu {
  z-index: 2;
  opacity: 1;
  transform: translateY(1);
  pointer-events: visible;
}

@media (max-width: 991.98px) {
  .menu__btn {
    display: flex;
  }
}